.page {
  font-family: Montserrat, BlinkMacSystemFont, Roboto, Helvetica Neue, sans-serif; }

.page-header {
  display: flex;
  justify-content: center;
  background-color: #ffffff;
  height: 50px; }
  .page-header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 83%; }
  .page-header__icons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 60px; }
  .page-header__basket {
    cursor: pointer; }
  .page-header__burger {
    cursor: pointer; }
    @media (min-width: 768px) {
      .page-header__burger {
        display: none; } }
  .page-header__phone {
    display: none;
    cursor: pointer; }
    @media (min-width: 768px) {
      .page-header__phone {
        display: block; } }
  .page-header__nav {
    display: none; }
    @media (min-width: 768px) {
      .page-header__nav {
        display: flex; } }

.work-time__header {
  display: none; }
  @media (min-width: 768px) {
    .work-time__header {
      display: block; } }

.main-nav__toggle {
  display: none; }

.main-nav__list {
  display: flex; }

.book-card {
  display: flex;
  width: 220px; }
  @media (min-width: 768px) {
    .book-card {
      width: 260px; } }
  .book-card__link {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap; }
  .book-card__title {
    order: 2; }
  .book-card__img {
    order: 1;
    height: 315px;
    width: 220px; }
  @media (min-width: 768px) {
    .book-card {
      height: 370px;
      width: 260px; } }
  .book-card__descr {
    order: 3; }
  .book-card__price {
    order: 4; }

.promo {
  background-color: #f5f5f5; }
  .promo__inner {
    width: 83%;
    margin: 0 auto; }

.catalog {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .catalog__title {
    display: none; }
  .catalog__content {
    display: flex; }
  .catalog__cards {
    display: flex;
    flex-direction: column;
    align-items: center; }
    @media (min-width: 768px) {
      .catalog__cards {
        flex-direction: row;
        flex-wrap: wrap; } }
  .catalog__pagination {
    display: flex;
    justify-content: space-around; }

.slider__item {
  display: flex;
  flex-direction: column; }

.slider__descr-wrap {
  border: 8px solid #e8e8e8;
  padding: 30px 10px; }

.slider__item-img {
  width: 0; }
  @media (min-width: 1200px) {
    .slider__item-img {
      width: 300px;
      height: 425px; } }

.new__catalog {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  overflow-x: auto; }

.page-footer__inner {
  display: flex;
  justify-content: space-between; }

.page-footer__nav {
  display: none; }
  @media (min-width: 768px) {
    .page-footer__nav {
      display: flex; } }

.tabs {
  width: 100%; }
  .tabs__inner {
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;
    overflow-x: auto;
    background-color: #f2f2f2; }
  .tabs__link--active {
    border-bottom: 3px solid #ef529f; }

.delivery__list {
  display: flex;
  flex-direction: column;
  align-items: center; }

.delivery__item {
  display: flex;
  flex-direction: column;
  align-items: center; }
